.details{position:relative}.details:after{top:-10px;transition:all .2s ease-in-out;content:"expand_more";position:absolute;right:0;display:flex;justify-content:center;align-items:center;width:44px;height:44px;font-size:24px;font-family:material icons,sans-serif;color:rgba(31,32,65,.5);pointer-events:none}.details[open]:after{transform:rotate(180deg);transition:all .2s ease-in-out}.details__title{overflow:hidden;padding:5px 44px 5px 0;white-space:nowrap;text-overflow:ellipsis;cursor:pointer}.details__items{padding:11px 0 0}