.colors-and-type{background:#fff}.colors-and-type__wrapper{display:flex;justify-content:space-between;flex-wrap:wrap;margin:35px 10px 145px}.colors-and-type__column{width:calc(50% - 92px)}@media screen and (max-width:1200px){.colors-and-type__column{width:calc(50% - 5px)}}@media screen and (max-width:600px){.colors-and-type__column{width:100%}}.color{display:flex;align-items:center;margin-bottom:21px;font-size:19px;line-height:29px;color:#1f2041}.color__sample{width:70px;height:70px;border-radius:6px}.color__sample.is-black{background:#1f2041}.color__sample.is-dark75{background:rgba(31,32,65,.75)}.color__sample.is-dark50{background:rgba(31,32,65,.5)}.color__sample.is-dark25{background:rgba(31,32,65,.25)}.color__sample.is-dark5{background:rgba(31,32,65,.05)}.color__sample.is-purple{background:#bc9cff}.color__sample.is-green{background:#6fcf97}.color__caption{flex:1;margin-left:40px}@media screen and (max-width:900px){.color__caption{margin-left:20px}}.type{display:flex;align-items:flex-start;margin-bottom:50px}.type__tag{width:70px;margin-right:30px;text-align:right;color:rgba(31,32,65,.25)}.type__tag--heading-level1{font-size:24px;line-height:30px}.type__tag--heading-level2{font-size:19px;line-height:24px}.type__tag--heading-level3{font-size:12px;line-height:15px}@media screen and (max-width:900px){.type__tag{margin-right:20px}}.type__text{flex:1}@media screen and (max-width:900px){.type{margin-top:40px}}