.btn{position:relative;display:inline-flex;font-size:12px;line-height:15px;font-weight:700;text-align:center;text-transform:uppercase;color:#bc9cff;cursor:pointer;user-select:none}.btn--action{width:100%}.btn--action__field,.btn--primary__field,.btn--secondary__field{position:absolute;width:100%;height:100%;border-radius:22px;background:linear-gradient(180deg,#bc9cff,#8ba4f9)}.btn--action__title,.btn--primary__title,.btn--secondary__title{z-index:1;overflow:hidden;height:44px;border-radius:22px;padding:0 18px 2px;line-height:42px;white-space:nowrap;pointer-events:none;text-overflow:ellipsis}.btn--action__title,.btn--primary__title{border:2px solid transparent;color:#fff}.btn--action__title{width:100%;padding-right:44px;padding-left:44px}.btn--action__arrow:after{font-weight:400;color:#fff;content:"arrow_forward";position:absolute;right:0;bottom:0;display:flex;justify-content:center;align-items:center;width:44px;height:44px;font-size:24px;line-height:24px;font-family:material icons,sans-serif;pointer-events:none}.btn--secondary__title{border:2px solid transparent;background:#fff;background-clip:padding-box}.btn--inline__field{position:absolute;width:100%;height:100%}.btn--inline__title{margin:-5px;padding:5px;white-space:nowrap;color:#bc9cff}.btn:hover [class$=__field]{opacity:.5;transition:all .2s ease-in-out}.btn:hover [class$="--inline__title"]{color:rgba(31,32,65,.5);transition:all .2s ease-in-out}