.dropdown{position:relative}.dropdown__title{margin-bottom:5px;cursor:default}.dropdown__caption{position:absolute;top:0;right:0;cursor:default;font-size:12px;line-height:15px;text-transform:uppercase;white-space:nowrap;color:rgba(31,32,65,.5)}.dropdown__field{cursor:default;display:flex;align-items:center;width:100%;height:44px;padding:0 15px;line-height:40px;color:rgba(31,32,65,.75);border:1px solid rgba(31,32,65,.25);border-radius:4px}.dropdown__field-text{margin-right:29px;overflow:hidden;white-space:nowrap;text-overflow:ellipsis}.dropdown__field:after{content:"expand_more";position:absolute;right:0;display:flex;justify-content:center;align-items:center;width:44px;height:44px;font-size:24px;font-family:material icons,sans-serif;color:rgba(31,32,65,.5);pointer-events:none}.dropdown__options{position:absolute;z-index:10;visibility:hidden;overflow:hidden;width:100%;border:1px solid rgba(31,32,65,.5);border-top:none;border-radius:0 0 4px 4px;padding:7px 7px 2px 15px;background:#fff;opacity:0}.dropdown__buttons{display:flex;justify-content:space-between;flex-direction:row-reverse;margin:19px 0 11px}.dropdown__buttons .is-hidden{display:none}.dropdown.is-open--forced .dropdown__field,.dropdown.is-open .dropdown__field{box-shadow:0 10px 20px rgba(31,32,65,.05);border-radius:4px 4px 0 0;border-color:rgba(31,32,65,.5);transition:all .2s ease-in-out}.dropdown.is-open--forced .dropdown__options,.dropdown.is-open .dropdown__options{visibility:visible;box-shadow:0 10px 20px rgba(31,32,65,.05);opacity:1;transition:all .2s ease-in-out}.dropdown.is-open--forced{z-index:9}.option{display:flex;justify-content:space-between;align-items:center;margin-bottom:7px}.option__title{flex:1;padding-right:5px;overflow:hidden;white-space:nowrap;text-overflow:ellipsis}.option__change{display:flex;justify-content:center;align-items:center;width:30px;height:30px;border:1px solid rgba(31,32,65,.25);border-radius:15px;font-size:18px;line-height:22px;color:rgba(31,32,65,.25);user-select:none}.option__change.is-active{border-color:rgba(31,32,65,.25);color:rgba(31,32,65,.5);cursor:pointer}.option__change.is-active:hover{transition:.2s ease-in-out}.option__count{width:30px;text-align:center}