.form-elements{background:#fff}.form-elements__wrapper{display:flex;justify-content:space-between;flex-wrap:wrap;margin-top:35px;margin-bottom:30px}.form-elements__column{width:calc(33.33333% - 66.66667px)}.form-elements__column--wide{width:calc(66.66667% - 60.66667px)}@media screen and (max-width:900px){.form-elements__column--wide{width:100%}}.form-elements__column:nth-child(2),.form-elements__column:nth-child(5),.form-elements__column:nth-child(8){margin-left:52px}@media screen and (max-width:1200px){.form-elements__column:nth-child(2),.form-elements__column:nth-child(5),.form-elements__column:nth-child(8){margin-left:0}}.form-elements__column.with-wide-margin{margin-bottom:102px}@media screen and (max-width:1200px){.form-elements__column.with-wide-margin{margin-bottom:41px}}@media screen and (max-width:1200px){.form-elements__column{width:calc(50% - 50px);margin-bottom:30px}}@media screen and (max-width:900px){.form-elements__column{width:calc(50% - 20px)}}@media screen and (max-width:600px){.form-elements__column{width:100%}}.form-elements__subtitle{margin-bottom:16px}.form-elements__narrow-column{width:100%;max-width:266px}@media screen and (max-width:900px){.form-elements__narrow-column{max-width:100%}}.form-elements__text-field{margin-bottom:21px}.form-elements__calendar{margin:41px 0}.form-elements__checkboxes{margin-bottom:41px}.form-elements__checkbox{margin-bottom:5px}.form-elements__radios{display:flex;margin-top:-2px;margin-bottom:37px}.form-elements__radio{margin-right:20px}.form-elements__toggle{margin-top:-2px;margin-bottom:10px}.form-elements__toggle--last{margin-bottom:40px}.form-elements__likes{margin-bottom:38px}.form-elements__like{margin-right:10px}.form-elements__rates{margin-bottom:40px}.form-elements__rate{margin-right:20px}.form-elements__buttons{display:flex;margin-bottom:30px}.form-elements__buttons--last{position:relative;margin-bottom:40px}.form-elements__button{margin-right:18px}.form-elements__button--inline{margin:-5px 20px 0 0}.for