.advantage{display:flex}.advantage__icon{display:flex;justify-content:center;align-items:center;width:48px;height:48px}.advantage__icon:before{background:linear-gradient(180deg,#bc9cff,#8ba4f9);-webkit-background-clip:text;-webkit-text-fill-color:transparent;font-size:48px;line-height:41px;font-family:material icons,sans-serif}.advantage__icon--face:before{content:"insert_emoticon"}.advantage__icon--city:before{content:"location_city"}.advantage__description{display:flex;justify-content:center;flex-direction:column;margin-left:10px}.advantage__title{line-height:24px}