.dd-dates{position:relative}.dd-dates__fields{display:flex;justify-content:space-between}.dd-dates__end,.dd-dates__start{width:calc(50% - 10px)}.dd-dates__title{margin-bottom:5px;cursor:default}.dd-dates__field{position:relative;cursor:default;display:flex;align-items:center;width:100%;height:44px;padding:0 15px;line-height:40px;color:rgba(31,32,65,.75);border:1px solid rgba(31,32,65,.25);border-radius:4px}.dd-dates__field-text{margin-right:29px;overflow:hidden;white-space:nowrap;text-overflow:ellipsis}.dd-dates__field:after{content:"expand_more";position:absolute;right:0;display:flex;justify-content:center;align-items:center;width:44px;height:44px;font-size:24px;font-family:material icons,sans-serif;color:rgba(31,32,65,.5);pointer-events:none}.dd-dates__calendar{position:absolute;top:calc(100% + 5px);right:0;left:0}