body{display:flex;flex-direction:column;background:#fff;font-size:14px;line-height:1.7;color:rgba(31,32,65,.75);font-family:montserrat,-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen,Ubuntu,Cantarell,Fira Sans,Droid Sans,Helvetica Neue,sans-serif}.wrapper{min-width:320px;max-width:1600px;margin:0 auto;padding:0 140px}@media screen and (max-width:1200px){.wrapper{padding:0 64px}}@media screen and (max-width:900px){.wrapper{padding:0 32px}}@media screen and (max-width:600px){.wrapper{padding:0 16px}}.heading{font-weight:700;color:#1f2041}.heading--level1{font-size:24px;line-height:30px}.heading--level1,.heading--level2{font-family:open sans,-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen,Ubuntu,Cantarell,Fira Sans,Droid Sans,Helvetica Neue,sans-serif}.heading--level2{font-size:19px;line-height:24px}.heading--level3{font-size:12px;line-height:15px;text-transform:uppercase}.heading--level4{line-height:18px;font-weight:700}.heading--level4,.paragraph{font-size:14px;color:rgba(31,32,65,.75)}.paragraph{line-height:24px}.list>*{position:relative;margin:0 0 10px 20px}.list>:before{content:"";position:absolute;top:7px;left:-20px;width:10px;height:10px;border-radius:5px;background:rgba(31,32,65,.25)}.icon{display:inline-block;font-size:1em;line-height:1;font-family:material icons,sans-serif;text-transform:none;letter-spacing:normal;word-wrap:normal;white-space:nowrap;user-select:none}